.left-container{
    border: 0.1em solid grey;
    width: 20vw;
    height: 100vh;
    background-color: black;
    color: white;
    box-shadow: 30px 40px white;
}

.logo img{
    margin-left: -2em;
    margin-top: 11vw;
}

.logo{
    width: 11.8vw;
    margin: auto;
    font-family: 'Raleway';
    /* border-top: 1rem solid orange; */
}
.search{
    margin-top: 3em;
}

.search input{
    background: black;
    width: 16vw;
    height: 2em;
    margin-left: 1em;
    color: white;
    border-radius: 0.5em;
    border: 1px solid orangered;
    padding: 0.4em 1em;
}

.search ::after{
    border: 1px solid orangered;
}

.search button{
    cursor:pointer;
    width: 8em;
    margin-left: 3.7em;
    margin-top: 0.8em;
    font-size: 1.1em;
    background: #bf3825;
    padding: 0.4em;
    border-radius: 0.4em;
}

.search button:hover{
    background-color: black;
    color: #bf3825;
    border: 0.1rem solid #bf3825;
}

.filters{
    margin-top: 4vh;
}

.filters span{
    cursor:pointer;
    font-size: 1.4vw;
    font-weight: 400;
    margin-left: 5vw;
    color: #bf3825;
}

.dropDown li{
    cursor:pointer;
    list-style: none;
    font-size: 1.4vw;
    color:#bf3825;
    font-weight: 400;
}

.dropDown ul{
    padding-left: 1vw;
    margin-top: 0.3vw;
    margin-bottom: 0.4vw;
}


.dropDown{
    border: 1px solid #bf3825;
    width: 10vw;
    margin-left: 4vw;
    border-radius: 0.5vw;
}

.priceDropDown input{
    width:4vw
}
