.rightContainer {
  /* border: 1px solid blue; */
  background-image: linear-gradient(to right, rgb(3, 3, 3), rgb(54, 54, 54));
  width: 80vw;
  flex: 1 1 auto;
}

.rightContainer h2 {
  color: rgb(238, 53, 53);
}

.boksContainer {
  width: 75vw;
  height: 80vh;
  /* border: 1px solid red; */
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
}
.box {
  display: flex;
  flex: 40 40;
  border: 1px solid grey;
  margin: 1vw;
  border-radius: 1rem;
}

.box:hover {
  box-shadow: 0.3rem 0.5rem 0.5rem rgb(100, 99, 99);
}

.boksContainer img {
  height: 15vw;
  margin: 1vw;
}

.title {
  color: rgb(238, 53, 53);
}

.author {
  color: rgb(238, 53, 53);
}
.publisher {
  color: rgb(238, 53, 53);
}
.price {
  color: rgb(238, 53, 53);
}

.infoContainer {
  margin: 1vw;
  width: 15vw;
}

.btn {
  display: flex;
}
.btn span {
  color: grey;
  margin-top: 3vh;
  margin-left: 1vw;
}

.btn input {
  color: hwb(84deg 34% 10%);
  margin-top: 3vh;
  margin-left: 2vw;
  width: 3vw;
  background: black;
  border: 1px solid #bf3825;
  border-radius: 1.2vh;
}

.btn button {
  width: 8em;
  margin-left: 3.7em;
  margin-top: 0.8em;
  font-size: 1em;
  background: #bf3825;
  padding: 0.4em;
  border-radius: 0.4em;
}

.btn button:hover {
  background-color: rgb(31, 31, 31);
  color: #bf3825;
  border: 0.1rem solid #bf3825;
}
